<template lang="pug">
    .progress-wrapper
        progress(:style="{ width: calcLength + 'px' }")
</template>

<script>
    export default {
        name: "Progress",
        props: {
            length: {
                default: 0,
                type: Number,
            }
        },
        computed: {
            calcLength() {
                if (this.length) {
                    return (1 - Math.pow(Math.E, -this.length / 200)) * 230;
                }
                return 0;
            }
        }
    }
</script>

<style scoped lang="stylus">
    .progress-wrapper
        width 230px
        height 100%
        display flex
        align-items center
    progress
        display inline-block
</style>
