<template lang="pug">
    .wrapper
        .main
            .table
                .row.tr(v-if="skills.length === 0")
                    span 什么都没有，快去添加几个要练习的技能吧～
                template(v-for='(skill, index) in skills')
                    .row
                        .tr(:key="index")
                            span.td.name {{ skill.name }}
                            span.td.total {{ skill.total }}
                            Progress(:length="+skill.total")
                            span.td.today {{ skill.today }}
                            span.add(@click="increase(index)") +
                        span.del(@click="del(skill.name)") +
            .control
                .buttons
                    .button(@click="showAddDialog") 新增
                    .button(@click="calcForToday") 结算
                    .button(@click="showHelpDialog") 帮助
                .time.title 当前时间 {{ time }}
                v-chart.chart(v-if="!chartLoading" :options="chart")
        el-dialog(:visible.sync="dialogAdd.show" width="500px" top="300px").modal
            .title 请输入名称
            input.input(v-model="dialogAdd.text")
            .title 请输入初始值
            input.input(v-model="dialogAdd.total" placeholder="不输入默认为0")
            .footer
                .dialog-button(@click="cancelAdd") 取消
                .dialog-button(@click="confirmAdd") 确定
        el-dialog(:visible.sync="dialogDel.show" width="500px" top="300px").modal
            .title 确定要删除{{ dialogDel.delId }}吗？
            .footer
                .dialog-button(@click="cancelDel") 取消
                .dialog-button(@click="confirmDel") 确定
        el-dialog(:visible.sync="dialogHelp.show" width="500px" top="300px").modal
            .title.help {{ dialogHelp.text }}
            .footer
                .dialog-button(@click="closeHelpDialog") 我知道了
</template>

<script>
    import Progress from "./Progress";

    const dialogAddDefault = {
        show: false,
        text: '',
        total: undefined,
    };

    const dialogDelDefault = {
        show: false,
        delId: undefined,
    };

    const dialogHelpDefault = {
        show: false,
        text: `Hello~这里是帮助\n
1. 左边是技能条，每一栏包含名字，技能点和当前未结算的技能点，平时练习的时候可以通过点击+按钮添加技能点，完成之后点击结算按钮，计入到总的技能点当中。\n
2. 每天第一次进入的时候，如果有前一天没有结算的技能点，会自动结算。\n
        `,
    };

    export default {
        name: 'HelloWorld',
        components: {Progress},
        watch: {
            skills: {
                deep: true,
                handler: (val) => {
                    localStorage.setItem('todo/skills', JSON.stringify(val));
                },
            },
            daily: {
                deep: true,
                handler: (val) => {
                    localStorage.setItem('todo/daily', JSON.stringify(val));
                },
            },
            currentDay(val) {
                localStorage.setItem('todo/current', JSON.stringify(val));
            },
        },
        methods: {
            day7Category() {
                return this.daily.slice(-7).map(v => v.day);
            },
            day7Value() {
                return this.daily.slice(-7).map(v => v.point);
            },
            calc(day) {
                let total = 0;
                this.skills.forEach(skill => {
                    skill.total = +skill.total + skill.today;
                    total += skill.today;
                    skill.today = 0;
                });
                const current = this.daily[this.daily.length - 1];
                if (current && total) {
                    if (current.day === day) {
                        current.point += total;
                    } else {
                        this.daily.push({
                            day: day,
                            point: total,
                        });
                    }
                } else if (total){
                    this.daily.push({
                        day: day,
                        point: total,
                    });
                }
                this.resetChartData();
            },
            calcForToday() {
                const date = new Date();
                const dayStr = `${date.getFullYear()}/${date.getMonth()+1}/${date.getDate()}`;
                this.calc(dayStr);
            },
            calcForLastDay() {
                let date = Date.now();
                date -= 1000 * 60 * 60 * 24;
                date = new Date(date);
                const dayStr = `${date.getFullYear()}/${date.getMonth()+1}/${date.getDate()}`;
                const newStr = `${date.getFullYear()}/${date.getMonth()+1}/${date.getDate() + 1}`;
                if (newStr !== this.currentDay) {
                    this.calc(dayStr);
                    this.currentDay = newStr;
                } else {
                    this.resetChartData();
                }
            },
            increase(index) {
                this.skills[index].today += 1;
            },
            cancelDel() {
                this.dialogDel.delId = undefined;
                this.dialogDel.show = false;
            },
            confirmDel() {
                this.skills = this.skills.filter(v => v.name !== this.dialogDel.delId);
                this.dialogDel.show = false;
                this.dialogDel.delId = undefined;
            },
            del(name) {
                this.dialogDel.show = true;
                this.dialogDel.delId = name;
            },
            showHelpDialog() {
                this.dialogHelp.show = true;
            },
            closeHelpDialog() {
                this.dialogHelp.show = false;
            },
            showAddDialog() {
                this.dialogAdd.show = true;
            },
            cancelAdd() {
                this.dialogAdd.text = '';
                this.dialogAdd.show = false;
                this.dialogAdd.total = undefined;
            },
            confirmAdd() {
                this.dialogAdd.total = (this.dialogAdd.total || '').replace(/\D/g, '');
                this.skills.push({
                    name: this.dialogAdd.text,
                    total: this.dialogAdd.total || 0,
                    today: 0,
                });
                this.dialogAdd.text = '';
                this.dialogAdd.total = undefined;
                this.dialogAdd.show = false;
            },
            resetChartData() {
                this.chartLoading = true;
                this.chart.xAxis.data = this.day7Category() || [];
                this.chart.series[0].data = this.day7Value();
                if (this.chart.xAxis.data.length === 0) {
                    for (let i = 0; i < 7; i ++) {
                        this.chart.xAxis.data.push('sample');
                        this.chart.series[0].data.push(Math.floor(Math.random() * 20));
                    }
                } else if(this.chart.xAxis.data.length === 1) {
                    this.chart.xAxis.data.unshift('sample');
                    this.chart.series[0].data.unshift(Math.floor(Math.random() * 20));
                }
                this.chartLoading = false;
            },
        },
        mounted() {
            setInterval(() => this.time = new Date(), 1000);
            const skills = JSON.parse(localStorage.getItem('todo/skills'));
            if (skills && skills !== 'null') {
                this.skills = skills;
            }
            const daily = JSON.parse(localStorage.getItem('todo/daily'));
            if (daily && daily !== 'null') {
                this.daily = daily;
            }
            this.currentDay = JSON.parse(localStorage.getItem('todo/current'));
            this.calcForLastDay();
        },
        data() {
            return {
                time: new Date(),
                dialogAdd: Object.assign({}, dialogAddDefault),
                dialogDel: Object.assign({}, dialogDelDefault),
                dialogHelp: Object.assign({}, dialogHelpDefault),
                currentDay: '',
                daily: [],
                skills:[],
                chartLoading: true,
                chart: {
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'cross'
                        }
                    },
                    xAxis: {
                        type: 'category',
                        boundaryGap: false,
                        data: [],
                    },
                    yAxis: {
                        type: 'value'
                    },
                    series: [{
                        color: 'rgba(255, 193, 56, 0.5)',
                        data: [],
                        type: 'line',
                        areaStyle: {
                            color: '#36a4ff',
                            opacity: 0.3,
                        }
                    }]
                }
            }
        },
    }
</script>

